<template>
    <div class="mb-3">
        <label for="exampleFormControlTextarea1" class="form-label toop">吐槽一下</label>
        <textarea v-model="content" class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
        <button @click="submit_a_post" type="button" class="btn btn-success btn-sm">发送吐槽</button>
    </div>
</template>

<script>
import { ref } from 'vue';

export default {
    name: "UserProfileWrite",

    setup(props, context) {
        let content = ref('');

        const submit_a_post = () => {
            context.emit("submit_a_post", content.value);
            content.value = "";
        }
        return {
            content,
            submit_a_post,
        }
    }
}
</script>

<style scoped>
.toop {
    margin-top: 20px;
}
</style>